import React from 'react';
import {connect} from 'react-redux';
import {Container, Row, Col} from 'react-bootstrap';
import {Table} from 'react-bootstrap';
import Loading from '../Loading';
import {getCustomers} from '../../AC';
import Modal from '../Modal';
import CreateCustomerForm from './CreateCustomerForm';

class CustomerList extends React.Component {
    constructor(props){
        super(props);
        this.title = 'Create a new customer';
    }

    componentWillMount(){
        const {getCustomers} = this.props;
        getCustomers();
        
    }

    render(){
        const {customers} = this.props;
        if (customers.loading) return <Loading />;
        let customersList;
        if(customers.data.length){customersList = customers.data.map((customer, idx) => <tr key={customer.id}><td>{idx + 1}</td><td>{customer.name}</td><td>{customer.address}</td><td>{customer.phone}</td></tr>)};
        
        return (
            <Container>
            <Row className="align-items-center">
                <Col lg={3}>
            <h2>Customer List</h2>
            </Col><Col>
            <Modal title={this.title} create={<CreateCustomerForm/>}/>
            </Col>
            </Row>
            <Row>
            <Col>
            <Table responsive>
            <thead>
            <tr>
               <th>#</th>
               <th>Name</th>
               <th>Address</th>
               <th></th>
               </tr>
            </thead>
            <tbody>{customersList}</tbody>
            </Table>
            </Col>
            </Row>
            </Container>
        )
    }
}


export default connect((state) => ({
    customers: state.customers
}), { getCustomers })(CustomerList);