import React from 'react';
import {connect} from 'react-redux';
import {Container, Row, Col} from 'react-bootstrap';
import {Table} from 'react-bootstrap';
import Loading from '../Loading';
import {getProducts} from '../../AC';
import Modal from '../Modal';
import CreateProductForm from './CreateProductForm';

class ProductList extends React.Component {
    constructor(props){
        super(props);
        this.title = 'Create a new product';
    }
    
    componentWillMount(){
        const {getProducts} = this.props;
        getProducts();
    }

    render(){

        const {products} = this.props;
        if (products.loading) return <Loading />;
        let productList;
        if(products.data.length){productList = products.data.map((product, idx) => <tr key={product.id}><td>{idx + 1}</td><td>{product.name}</td><td>{product.price}</td></tr>)};
        
        return (
            <Container>
            <Row className="align-items-center">
            <Col lg={3}>
            <h2>Product List</h2>
            </Col><Col>
            <Modal title={this.title} create={<CreateProductForm/>}/>
            </Col>
            </Row>
            <Row>
            <Col>
            <Table responsive>
            <thead>
            <tr>
               <th>#</th>
               <th>Name</th>
               <th>Price</th>
               </tr>
            </thead>
            <tbody>{productList}</tbody>
            </Table>
            </Col>
            </Row>
            </Container>
        )
    }
}


export default connect((state) => ({
    products: state.products
}), { getProducts })(ProductList);