import React from 'react';
import {connect} from 'react-redux';
import { Form, Button, Col } from 'react-bootstrap';
import { createProduct } from '../../AC';

function CreateProductForm(props) {
    const [validated, setValidated] = React.useState(false);
    let newProduct = {};

    const handleChange = event => {
      const placeholder = event.currentTarget.placeholder;
      const value = event.currentTarget.value;

      switch(placeholder){
        case 'Name':
          newProduct.name = value;
            break;
        case 'Price':
          newProduct.price = value;
            break;          
      }
    }

    const handleSubmit = event => {

      const form = event.currentTarget;
      const { createProduct } = props;
      if (form.checkValidity()) {
        return createProduct(newProduct);
      }
      setValidated(true);
      event.preventDefault();
      event.stopPropagation();
    } 
    return (
      <Form noValidate validated={validated} onSubmit={handleSubmit}>
        <Form.Row>
          <Form.Group as={Col} controlId="validationCustom01">
            <Form.Label>Product name</Form.Label>
            <Form.Control
              onChange={handleChange}
              required
              type="text"
              placeholder="Name"
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
        <Form.Group as={Col} md="3" controlId="validationCustom02">
            <Form.Label>Price</Form.Label>
            <Form.Control onChange={handleChange} type="number" placeholder="Price" required />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
        </Form.Row>
        <Button type="submit">Create</Button>
      </Form>
    );
  }

  export default connect((state) => ({
    product: state.product
}), { createProduct })(CreateProductForm);