import {START, SUCCESS, FAIL, GET_CUSTOMERS, CREATE_CUSTOMER} from '../constants';

const initState = {
    data: [],
    loading: false,
    error: false
}

export default (customers = initState, action) => {
    const {type, payload} = action;
    
    switch(type){ 
        
        case GET_CUSTOMERS + START:

        return {
            loading: true
        };

        case GET_CUSTOMERS + SUCCESS:

        return {
            data: payload.customers,
            loading: false
        };

        case GET_CUSTOMERS + FAIL:

        return {
            data: payload.customers,
            loading: false,
            error: true
        };

                case CREATE_CUSTOMER + SUCCESS:
        
                return {
                    ...customers,
                    data: payload.customer
                };
        
                case CREATE_CUSTOMER + FAIL:
        
                return {
                    error: true
                };

    }
    return customers
}