import React from 'react';
import {connect} from 'react-redux';
import { Form, Button, Col } from 'react-bootstrap';
import { createCustomer } from '../../AC';


function CreateCustomerForm(props) {
    
    const [validated, setValidated] = React.useState(false);   

    let newCustomer = {};

    const handleChange = event => {
      const placeholder = event.currentTarget.placeholder;
      const value = event.currentTarget.value;

      switch(placeholder){
        case 'Name':
            newCustomer.name = value;
            break;
        case 'Address':
            newCustomer.address = value;
            break;
        case 'Phone':
            newCustomer.phone = value;
            break;            
      }
    }
    const handleSubmit = event => {
      const form = event.currentTarget;
      const { createCustomer } = props;
      if (form.checkValidity()) {
        return createCustomer(newCustomer);
      }
      setValidated(true);
      event.preventDefault();
      event.stopPropagation();
    } 
    return (
      <Form noValidate validated={validated}  onSubmit={handleSubmit}>
        <Form.Row>
          <Form.Group as={Col} controlId="validationCustom01">
            <Form.Label>First and Last name</Form.Label>
            <Form.Control
              required
              type="text"
              placeholder="Name"
              onChange={handleChange}
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
        </Form.Row>
        <Form.Row>
        <Form.Group as={Col} md="6" controlId="validationCustom02">
            <Form.Label>Address</Form.Label>
            <Form.Control onChange={handleChange} type="text" placeholder="Address" required />
            <Form.Control.Feedback type="invalid">
              Please provide a valid address.
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} md="6" controlId="validationCustom03">
            <Form.Label>Phone</Form.Label>
            <Form.Control onChange={handleChange} type="text" placeholder="Phone" required />
            <Form.Control.Feedback type="invalid">
              Please provide a valid phone number.
            </Form.Control.Feedback>
          </Form.Group>
        </Form.Row>
        <Button type="submit">Create</Button>
      </Form>
    );
  }

  export default connect((state) => ({
    customer: state.customer
}), { createCustomer })(CreateCustomerForm);