import axios from 'axios';
import {START, SUCCESS, FAIL, GET_CUSTOMERS, CREATE_CUSTOMER, GET_PRODUCTS, CREATE_PRODUCT} from '../constants';

/* CUSTOMER ACTIONS */

export function getCustomers(){

    return (dispatch) => {
        dispatch({ type: GET_CUSTOMERS + START });
        axios.get('/api/customers').then(res => dispatch({
            type: GET_CUSTOMERS + SUCCESS,
            payload: {
                customers: res.data
            }
        })).catch(res => dispatch({
            type: GET_CUSTOMERS + FAIL,
            payload: {
                customers: res.error
            }
        }) 

        )}
}

export function createCustomer(newCustomer){

    return (dispatch) => {

        axios.post('/api/customers', newCustomer)
            .then(res => dispatch({
                type: CREATE_CUSTOMER + SUCCESS,
                payload: {
                    customer: res.data
                }
            }))
            .catch(res => dispatch({
                type: CREATE_CUSTOMER + FAIL,
                payload: {
                    customer: res.error
                }
            }))
    }
    
}

/* PRODUCT ACTIONS */

export function getProducts(){

    return (dispatch) => {
        dispatch({ type: GET_PRODUCTS + START });

        axios.get('/api/products')
        .then(res => dispatch({
            type: GET_PRODUCTS + SUCCESS,
            payload: {
                products: res.data
            }
        }))
        .catch(res => dispatch({
            type: GET_PRODUCTS + FAIL,
            payload: {
                products: res.error
            }
        }))
    }
}

export function createProduct(newProduct){
    
    return (dispatch) => {

        axios.post('/api/products', newProduct)
            .then(res => dispatch({
                type: CREATE_PRODUCT + SUCCESS,
                payload: {
                    product: res.data
                }
            }))
            .catch(res => dispatch({
                type: CREATE_PRODUCT + FAIL,
                payload: {
                    product: res.error
                }
            }))
    }
}